import React from 'react';

function Table({JsonData}) {

    const DisplayData = JsonData.map(
        (response) => {
            return (
                <tr key={response.id}>
                    <td>{response.x}</td>
                    <td>{response.y}</td>
                    <td>{response.r}</td>
                    <td>{response.value}</td>
                </tr>
            )
        }
    )

    return (
        <table>
            <thead>
                <tr>
                    <th>x</th>
                    <th>y</th>
                    <th>r</th>
                    <th>resp</th>
                </tr>
            </thead>
            <tbody>
                {DisplayData}
            </tbody>
        </table>
    );
}

export default Table;
